<template>
  <nav-bar>
    <span slot="title" class="title">员工管理 - {{worker.name}}</span>
  </nav-bar>
  <div class="content-form">
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label">员工名称</label>
      <div class="col-sm-10">
        <input class="form-control" placeholder="名称" type="text" v-model="worker.name" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label">所属部门</label>
      <div class="col-sm-10">
        <select prompt="请选择" class="c-select" v-model="worker.department">
          <option value="waiter">前台</option>
          <option selected="selected" value="kitchen">厨房</option>
          <option value="counter">总台</option>
        </select>
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label">员工账号</label>
      <div class="col-sm-10">
        <input class="form-control" placeholder="员工账号" type="text" v-model="worker.login" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputPassword3" class="col-sm-2 form-control-label">输入密码</label>
      <div class="col-sm-10">
        <input class="form-control" placeholder="输入密码" type="password" v-model="worker.password" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputPassword3" class="col-sm-2 form-control-label">确认密码</label>
      <div class="col-sm-10">
        <input class="form-control" placeholder="确认密码" type="password" v-model="worker.password_confirmation" />
      </div>
    </div>
    <div class="form-group row">
      <label for="inputEmail3" class="col-sm-2 form-control-label"></label>
      <div class="col-sm-10">
        <button @click="update" class="btn btn-primary">保 存</button>
      </div>
    </div>
  </div>
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        worker: {
          name: '',
          login: '',
          department: '',
          password: '',
          password_confirmation: ''
        }
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        var params = this.$route.params
        this.$http.get('http://jiancan.me/api/m1/workers/one.json', { access_token: this.access_token, worker_id: params.worker_id }).then(function (response) {
          transition.next({
            worker: response.data
          })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      update () {
        this.showLoading()

        var formData = new FormData()
        formData.append('access_token', this.access_token)
        formData.append('worker_id', this.$route.params.worker_id)
        formData.append('worker[name]', this.worker.name)
        formData.append('worker[login]', this.worker.login)
        formData.append('worker[department]', this.worker.department)
        formData.append('worker[password]', this.worker.password)
        formData.append('worker[password_confirmation]', this.worker.password_confirmation)

        this.$http.put('http://jiancan.me/api/m1/workers/one.json', formData).then(function (response) {
          this.$set('worker', response.data)
          this.$route.router.go({ path: '/workers' })
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



